.contendor_testimonio{
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    box-shadow: 0 3px 13px 1px rgb(0 0 0 /9%);
}

.imagen-testimonio{
    height: 100%;
    width: auto ;
    margin-right: 40px;

}

.contendor-texto-testimonio{
    text-align: center;
    font-family: Lato, sans-serif;
    font-weight: 400;
    line-height: 1.5rem;
    
}

.nombre-testimonio{
    font-size: 1.3rem;

}

.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;

}

.texto-testimonio{
    font-size: 1.1rem;
    text-align: justify;
    margin-right: 40px;
}